<script>
import { newcoinlog } from '@/api/user'
import { objKeySort, sgin } from '@/utils/auth'
import NavHead from '@/components/head.vue'
export default {
  components: { NavHead },
  data () {
    return {
      url: this.GLOBAL.domainName,
      languageStatus: 'zh',
      curHeight: '',
      list: [],
      parameter: {
