        activity: 'newcoin' // 固定值（newcoin）
      },
      title: '新币参与'
    }
  },
  created () {
    var data = objKeySort(this.parameter)
    var token = sgin(data)
    this.parameter.token = token
    this.voteList(this.parameter)
    this.beforeMount()
    this.getLanguageStatus()
  },
  methods: {
    getLanguageStatus () {
      console.log(this.$store.state.language)
      this.languageStatus = this.$store.state.language
    },
    beforeMount (height) {
      var h = document.documentElement.clientHeight || document.body.clientHeight
      this.curHeight = h
      console.log(this.curHeight, 'this.curHeight')
      // this.curHeight = h - height // 减去页面上固定高度height
    },
    add0 (m) { return m < 10 ? '0' + m : m },
    format (isTime) {
      var times = isTime * 1000
      var time = new Date(times)
      var y = time.getFullYear()
      var m = time.getMonth() + 1
      var d = time.getDate()
      var h = time.getHours()
      var mm = time.getMinutes()
      // console.log(y + '-' + this.add0(m) + '-' + this.add0(d) + ' ' + this.add0(h) + ':' + this.add0(mm) + ':' + this.add0(s))
      return y + '-' + this.add0(m) + '-' + this.add0(d) + ' ' + this.add0(h) + ':' + this.add0(mm)
    },
    voteList (parameter) {
      var that = this
      newcoinlog(parameter).then(res => {
        console.log(res, that)
        that.list = res.data
      }).catch((err) => console.log(err))
    }
  }
}
</script>
